import keyMirror from 'keymirror'

export default keyMirror({
  CLOCK_DATA_ERROR: true,
  CLOCK_MODE_CHANGED: true,
  CLOCK_MODE_STOPWATCH: true,
  CLOCK_MODE_TIMER: true,
  CLOCK_PAUSED: true,
  CLOCK_RESET: true,
  CLOCK_STARTED: true,
  CLOCK_TICK: true,
  CLOCK_TIMER_CHANGED: true,
  CLOCK_UNPAUSED: true
})



